@import "./mixins/gradients.less";

.n-lifecycle{
  position: relative;
  display: inline-block;
  height:34px;
  width:150px;
  z-index: 1;
  margin-left:-11px;
  margin-right:-11px;

  .n-lifecycle-content {
    &.bottom {
      transform: rotate(-45deg) skewY(45deg);
      width:88px;
      height: 24px;
      background-image: linear-gradient(to bottom, #eee 0%, #e7e7e7);
      border:1px solid #D7D7D7;
      position: absolute;
    }

    &.bottom:before{
      content:'';
      width:88px;
      height: 24px;
      top:23px;
      left:-13px;
      transform: skewX(-45deg);
      background-image: linear-gradient(to bottom, #e7e7e7, #ddd 100%);
      position: absolute;
      border:1px solid #D7D7D7;
    }

    &.top{
      width:106px;
      height:31px;
      top:5px;
      left:-9px;
      background-image: linear-gradient(to bottom, #eee 0%, #ddd 100%);
      position: absolute;
      & span{
        margin-top:10px;
        margin-left:5px;
        margin-right:5px;
        line-height: 14px;
        font-size: 14px;
        color: #474747;
        width:100%;
        text-align: center;
        position: absolute;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

      }
    }

    &.top:before {
      border-right:1px solid #D7D7D7;
      border-top:1px solid #D7D7D7;
      content:'';
      width:24px;
      height:24px;
      transform: rotate(45deg);
      top:3px;
      left:95px;
      position: absolute;
      background-image: linear-gradient(135deg, #eee 0%, #ddd 100%);
    }
  }
}


.n-lifecycle.selected{
  .n-lifecycle-content {
    &.bottom {
      background-image: linear-gradient(to bottom, #777777 0%, #525252);
      border:1px solid #353535;
      box-shadow: 0 0 2px #000000;
      z-index: -2;
    }

    &.bottom:before{
      background-image: linear-gradient(to bottom, #525252, #2f2f2f 100%);
      border:1px solid #353535;
      box-shadow: 0 0 2px #000000;
    }

    &.top{
      background-image: linear-gradient(to bottom, #777777 0%, #2f2f2f 100%);
      & span{
        color: #ffffff;
      }
    }

    &.top:before {
      border-right:1px solid #353535;
      border-top:1px solid #353535;
      box-shadow: 2px 0 2px #000000;
      background-image: linear-gradient(135deg, #777777 0%, #2f2f2f 100%);
      z-index: -1;
    }
  }
}

.n-lifecycle-start {
  left: -15px;
  margin-left:10px;
  .n-lifecycle-content {
    &.top{
      width:123px;
      height:34px;
      top:3px;
      border:1px solid #D7D7D7;
      border-right:none;
      border-top-left-radius: 7px;
      border-bottom-left-radius: 7px;

      &.selected{
        border:1px solid #353535;
        border-right:none;
        box-shadow: -2px 0 2px #000000;
      }
    }

    &.top:before {
      top:4px;
      left:110px;
    }

    &.top.selected:before{
      box-shadow: 1px 0 2px #000000;
    }
  }
}

.n-lifecycle-start.selected{
  .n-lifecycle-content {
    &.top{
      border:1px solid #353535;
      border-right:none;
      box-shadow: -2px 0 2px #000000;
    }

    &.top:before {
      box-shadow: 1px 0 2px #000000;
    }
  }
}

.n-lifecycle-end {
  .n-lifecycle-content {

    &.bottom {
      height:23px;
    }
    &.bottom:before {
      height:25px;
    }

    &.top{
      width:124px;
      height:34.5px;
      border:1px solid #D7D7D7;
      border-left:none;
      top:3px;
      left:-8px;
      border-top-right-radius: 7px;
      border-bottom-right-radius: 7px;

      &.selected{
        border:1px solid #353535;
        border-left:none;
        box-shadow: 2px 0 2px #000000;
      }
    }

    &.top:before {
      display: none;
    }
  }
}

.n-lifecycle-end.selected {
  .n-lifecycle-content {
    &.top{
      border:1px solid #353535;
      border-left:none;
      box-shadow: 2px 0 2px #000000;
    }
  }
}

