.n-drillDown-content {
    .icon-close-rounded {
        position: absolute;
        z-index: 3;
        top: 0;
        right: 30px;
        margin-top: 30px;
        cursor: pointer;
    }
    border-top: 1px solid rgba(255, 255, 255, .65);
    border-bottom: 1px solid rgba(255, 255, 255, .65);
    width: 100%;
    height: 370px;
    position: relative;
}

.n-drillDown-arrowContainer {
    height: 30px;
    position: relative;
}

.n-drillDown-collapsed {
    height: 400px;
    display: none;
    position: relative;
}

.n-drillDown-arrow:before {
    content: '';
    position: absolute;
    z-index: 8;
    width: 28px;
    height: 3px;
    margin-left: -14px;
    top: 21px;
    background: linear-gradient(to bottom, gray, gray);
}

.n-drillDown-arrow {
    width: 0;
    height: 0;
    position: absolute;
    border: 18px solid transparent;
    border-bottom:22px solid rgba(255, 255, 255, .65);
    border-top:none;
    z-index: 6;
    margin-left: -21px;
    top: 9px;
}

.n-drillDown-arrow:after {
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    border: 18px solid transparent;
    border-bottom: 22px solid gray;
    border-top-width: 0;
    top: 1px;
    margin-left: -18px;
    z-index: 7;
}

.n-drillDown-border {
    border-radius: 5px;
    box-shadow: 0 0 0 3px #00A1CC;
}

.n-drillDown-inner:before {
    content: '';
    background: -webkit-linear-gradient(rgba(0, 0, 0, .2), rgba(0, 0, 0, 0));
    background: -o-linear-gradient(rgba(0, 0, 0, .2), rgba(0, 0, 0, 0));
    background: -moz-linear-gradient(rgba(0, 0, 0, .2), rgba(0, 0, 0, 0));
    background: linear-gradient(rgba(0, 0, 0, .2), rgba(0, 0, 0, 0));
    position: absolute;
    top: 0;
    left: 0;
    height: 50px;
    width: 100%;
}

.n-drillDown-inner {
    padding: 30px;
    background: #D8D9DA;
    height: 100%;
    width: 100%;
    box-shadow: 12px 0 7px -12px rgba(0, 0, 0, .45) inset, -12px 0 7px -12px rgba(0, 0, 0, .45) inset, 0 12px 30px -12px rgba(0, 0, 0, .6) inset,0 -12px 30px -12px rgba(0, 0, 0, .2) inset;
}

.n-drillDown-inner:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    height: 50px;
    width: 100%;
}

.n-drillDown-item, .n-drillDown-row {
    cursor: pointer;
}

.n-drilldown-table tr:nth-child(4n+2) td {
    padding-left: 0;
    padding-right: 0;
    background-color: #efefef !important;

}

.n-drilldown-table tr:nth-child(4n) td {
    padding-left: 0;
    padding-right: 0;
    background-color: #ffffff !important;
}

.n-drilldown-table tr:nth-child(4n+3) td:not(.n-cell-selected) {
    background-color: #efefef;
}

.n-drilldown-table tr:nth-child(4n+1) td:not(.n-cell-selected) {
    background-color: #ffffff;
}

.n-drilldown-table tr {
    background-color: rgba(255, 255, 255, .1) !important;
}

.n-drilldown-table tr:last-child td {
    border-bottom-left-radius: 7px !important;
    border-bottom-right-radius: 7px !important;
}

.n-drilldown-table tr:nth-last-child(2) {
    > td {
        &:first-child {
            border-bottom-left-radius: 7px;
        }
        &:last-child {
            border-bottom-right-radius: 7px;
        }
    }
}

.n-drilldown-table .n-drillDown-inner {
    color: #1b1b1b;
}




