@import "../bower_components/bootstrap/less/mixins/gradients.less";

.scrollbars {
  position: relative;
  overflow: visible;

  .scrolling-content-container {
    overflow: hidden;
  }

  .scrolling-content {
    display: inline-block;
    position: relative;
  }

  .scrollbar-container {
    position: absolute;

    &.horizontal {
      height: 8px;
      left: 0;
      bottom: 0;

      .track {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 8px;
        background-color: rgba(213, 213, 213, .7);
        z-index: 10;
        border-radius: 4px;
        box-shadow: 0px -1px 0 #999;

        div {
          position: absolute;
          right: -10px;
          bottom: 0;
          height: 8px;
          width: 10px;
        }
      }

      .thumb {
        bottom: 0;
        left: 0;
        width: 100%;
        height: 8px;
        #gradient > .vertical(#999, #666);
        border-radius: 4px;
        position: absolute;
        display: block;
        cursor: pointer;
        z-index: 11;

        &:hover {
          #gradient > .vertical(#2691C9, #092454);
          color: #ffffff;
        }

        &:active {
          #gradient > .vertical(#333, #000);
        }

        &[disabled] {
          background-color: #a5a5a5;
          cursor: default;
        }
      }
    }

    &.vertical {
      width: 8px;
      right: 0;
      top: 0;

      .track {
        position: absolute;
        right: 1px;
        top: 0;
        bottom: 0;
        width: 8px;
        background-color: rgba(213, 213, 213, .7);
        border-radius: 4px;
        box-shadow: 1px 0 1px #999;

        div {
          position: absolute;
          right: 0;
          bottom: -10px;
          width: 8px;
          height: 10px;
        }
      }

      .thumb {
        top: 0;
        right: 0;
        width: 8px;
        height: 31px;
        #gradient > .horizontal(#999, #666);
        border-radius: 4px;
        position: absolute;
        display: block;
        cursor: pointer;
        z-index: 11;

        &:hover {
          #gradient > .horizontal(#2691C9, #092454);
          color: #ffffff;
        }

        &:active {
          #gradient > .horizontal(#333, #000);
        }

        &[disabled] {
          background-color: #a5a5a5;
          cursor: default;
        }
      }
    }
  }
}