@import "variables.less";

.nav-local{
  &:extend(.nav all);
}

.nav-local-horizontal{
  &:extend(.nav-secondary-horizontal all);

  #gradient > .vertical(@nav-local-horizontal-gradient-start, @nav-local-horizontal-gradient-end);
  .box-shadow(inset 0px 1px 0px 0px @gray-white);
  padding: 0;

  li {
    a {
      min-height: 44px;
      padding: 12px 22px;
    }

    &.selected {
      border-left-color: @nav-local-horizontal-border;
      border-right-color: @nav-local-horizontal-border;
      a {
        #gradient > .vertical(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 1));
        .box-shadow(inset 0 0 2px 0 rgba(0, 0, 0, 0.2));
        background-color: transparent;
      }
    }
  }
}

.nav-local-horizontal-row{
  margin: 0;
  z-index: 1;
  position: relative;
  border-top: 1px solid @nav-local-horizontal-border;
  border-bottom: 1px solid @nav-local-horizontal-border;
  .box-shadow(0px 2px 2px 0px rgba(0,0,0,0.2));
}

.nav-local-vertical{
  .box-shadow(3px 0px 3px 0px rgba(0, 0, 0, 0.2));
  background-color: @gray-white;

  li {
    min-height: 38px;

      a {
      border: 1px solid transparent;
      border-top-color: @nav-local-vertical-border-dark;
      border-right-color: @nav-local-vertical-border-dark;
      color: @gray-dark;
      font-weight: normal;
      font-size: 14px;
      padding: 9px 14px;
      outline: 0;

      &:hover {
        background-color: @gray-white;
        span{
          border-bottom: 1px solid @blue-nokia ;
        }
      }
    }

    &:first-child a{
      border-top-color: transparent;
    }

    &:not(:last-child){
      margin-bottom: -1px;
    }

    &.selected{
      a{
        border-color: @nav-local-vertical-border-blue;
        #gradient > .vertical(@nav-local-vertical-selected-gradient-start,@nav-local-vertical-selected-gradient-end);
        color: @gray-white;
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.9);
      }

      & + li a{
        border-top-color: @nav-local-vertical-border-blue;
      }
    }
  }

  &-round-left{
    border-bottom-left-radius: 7px;

    li:last-child{
      &.selected a,
      &:hover, a:hover{
        border-bottom-left-radius: 7px;
      }
    }
  }
}


.nav-local-menu{
  min-height: 30px;
  padding-left: 15px;
  border-top: 1px solid @gray-white;
  border-bottom: 1px solid @nav-local-menu-border-dark;
  #gradient > .vertical(@nav-local-menu-gradient-start, @nav-local-menu-gradient-end);
}

li.nav-local-menu-item{
  float: left;
  margin-right: 9px;
  padding: 4px 0;

  a{
    display: inline-block;
    font-size: 14px;
    color: @gray-nav;
    font-weight: normal;
    padding: 0;

    .icon{
      padding-right: 1px;
    }

    &:hover,
    &:focus{
      background-color: transparent;
      outline: 0;
      span{
        color: @black-hover;
        border-bottom: 1px solid @blue-nokia;
      }

      .caret, .icon{
        border-bottom: none;
      }
    }

    .caret{
      padding-bottom: 3px;
    }
  }

  &.selected {
    a{
      font-weight: bold;
    }
  }
}

.nav-local-menu-divided{
  &:extend(.nav-local-menu);

  .nav-local-menu-item{
    margin-right: 0;

    &:first-child a{
      padding-right: @nav-local-divided-padding;
    }
  }

  li+li:not(:first-child) a{
    .box-shadow(inset 1px 0px 0px 0px @nav-local-divider-right);
    border-left: 1px solid @nav-local-divider-left;
    padding: 0 @nav-local-divided-padding 0 9px;
  }
}

