@import "variables.less";

//OT3 P69 footer

//footer class without border
.n-footer {
    height: 40px;
    bottom: 0;
    width: 100%;
    padding: 10px 10px 0;

    .n-footer-left {
        float: left;
    }

    .n-footer-right {
        float: right;
    }
}

.n-footer-left {
    :not(:last-child) {
        margin-right: 15px;
    }
    ul {
        padding-left: 0;
    }
    li {
        list-style: none;
        display: inline-block;
        position: relative;
    }
}

.n-footer-right {

    .n-footer-copyright {
        display: inline-block;
        font-size: @font-size-small;
        color: @gray-global;
    }
}

//UI setup for the footer links
.n-footer-links {
    font-weight: normal;
    color: @gray-global;
    &:hover {
        color: @black-hover;
        border-bottom: 1px solid @blue-nokia;

    }
    &:focus {
        color: @black-hover;
    }
    //used to remove dotted border in IE and Firefox
    &:focus, &:hover, &:active {
        text-decoration: none;
        outline: none;
    }
}

//footer class used when border is required
.n-footer-bordered {
    border-top: 1px solid @footer-border-top;
    .n-footer;
}

//content wrapper which is used to wrap all non footer content
.n-footer-wrapper {
    min-height: calc(100% - 30px);
    line-height: 24px;
}